import React from 'react'
import Logo from './imgs/dia.jpg'

export default function Header(){
    return(
        <header className='conteiner'>
            <h1>Expressões em JSX. Inserção de imagens no App. Constantes e Variáveis</h1>
            <img src={Logo} alt="Bom dia"/>
            <h2>CFB Cursos</h2>
        </header>
    )

}